export default {
	data(){
		return{
			top: '100rpx',
			bottom: 0
		}
	},
	onShow() {
		let gao = 0
		let kuang = 0
		uni.getSystemInfo({
		    success: function (res) {
		        console.log(res.screenHeight);
		        console.log(res.windowHeight);
						kuang = res.screenWidth
						gao = res.windowHeight
		    }
		})
		console.log(gao)
		this.bottom = gao*750/kuang-100+'rpx'
	}
}
